
const url = 'https://bharat7.herokuapp.com/product/getProduct'

export function ProdutD(){
    const output = fetch("https://bharat7.herokuapp.com/product/getProduct",{method:'GET'})
    .then ((data)=>data.json())
    return {
        type:'DETAIL_PRODUCT',
        payload:output
    }   
}


export function IndiaLatestVideo(){
    const output = fetch("https://bharat7.herokuapp.com/product/getProduct",{method:'GET'})
    .then ((data)=>data.json())
    return {
        type:'INDIA_TRENDING_VIDEOS',
        payload:output
    }
}
